
* {
  /* width: 100%; */
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: sans-serif;
}
section {
  text-align: center;
}
.header {
  height: 70px;
  width: 100%;
  background-color: rgba(5, 143, 170, 0.6);
  margin-bottom: 1em;
  padding: 0.4em 0.8em;
  color: #fff;
  position: fixed;
  top: 0;
  z-index: 10;
}
.header-left {
  float: left;
}
.logo {
  width: 200px;
}
.content {
  margin: 150px auto 0;
  padding: 0 10px;
  max-width: 600px;
}
.form {
  position: fixed;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 80px;
  width: 100%;
  background: #f5f5f5;
  padding-top: 38px;
  padding-bottom: 38px;
  z-index: 2;
  bottom: 0px;
}
.form textarea {
  border: 1px solid #ccc;
  border-radius: 2px;
  height: 3.5em;
  width: calc(100% - 6em);
  resize: none;
  font-size: 16px;
}
.list {
  margin-bottom: 100px;
}
.item {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
  margin-bottom: 0.8em;
}
.item-image img {
  border-radius: 20px;
  vertical-align: top;
}
.item-detail {
  margin: 0 0 0 1.4em;
}
.item-name {
  font-size: 75%;
}
.item-message {
  position: relative;
  display: inline-block;
  padding: 0.8em;
  background: #deefe8;
  border-radius: 4px;
  line-height: 1.2em;
}
.item-message::before {
  position: absolute;
  content: " ";
  display: block;
  left: -16px;
  bottom: 12px;
  border: 4px solid transparent;
  border-right: 12px solid #deefe8;
}
.send-button {
  height: 4em;
}

/* トランジション用スタイル */
.chat-enter-active {
  -webkit-transition: all 1s;
  transition: all 1s;
}
.chat-enter {
  opacity: 0;
  -webkit-transform: translateX(-1em);
          transform: translateX(-1em);
}
a {
  text-decoration: none;
}
@media only screen and (max-device-width : 480px) {
img {
    width: 100%;
}
.logo {
    width: 150px;
}
.header {
    height: 60px
}
}

